<template>

    <div>
      <mt-header fixed :title="title" class="boder-header">
        <router-link to="/mine" slot="left">
          <mt-button icon="back"></mt-button>
        </router-link>
      </mt-header>
    <div class="wallet-container">

        <span>余额(元)</span>
        <span class="money">{{balance}}</span>
        <div class="center">
            <div class="btn" @click="goWithDraw">申请提现</div>
        </div>
        

        <div class="wallet-divider"></div>

        <wallet-tabs :activeIndex="activeIndex" :tabs="tabs" @tabSelected="tabSelected"></wallet-tabs>
        <wallet-income-tab v-if="activeIndex == 0"></wallet-income-tab>
        <wallet-expend-list v-if="activeIndex == 1"></wallet-expend-list>
    </div>
    </div>
</template>

<script>
import Vue from "vue";
import WalletTabs from '@/components/wallet/WalletTabs'
import WalletExpendList from "@/components/wallet/WalletExpendList";
import WalletIncomeTab from "@/components/wallet/WalletIncomeTab";

export default {
  props: {},
  data() {
    return {
      title: "我的钱包",
      balance: 0,
      isBind: false,
      activeIndex: 0,
      tabs: ["收入", "支出"],
    };
  },
  methods: {
    tabSelected(ind) {
      this.activeIndex = ind;
    },
    goWithDraw: function() {
      if(this.isBind==1){
        //申请提现
          this.$router.push({name: "申请提现"});
      }else{
        //绑卡
        this.$router.push({name: "addCard"});
      }
    }
  },
 components: { WalletTabs,WalletExpendList,WalletIncomeTab},
  created() {
      this.balance = sessionStorage.getItem('balance');
      this.isBind = sessionStorage.getItem('isBind');
  },
  mounted() {
  }
};
</script>
<style lang="postcss" scoped>
.wallet-container {
  display: flex;
  flex-direction: column;
  margin-top: 44px;
  & > span {
    font-size: 12px;
    color: #878da0;
    text-align: center;
    margin-top: 30px;
  }
  & .money {
    font-size: 36px;
    color: #334354;
    margin-top: 15px;
  }

  & .center {
    display: flex;
    justify-content: center;
    & .btn {
      width: 270px;
      height: 44px;
      color: #4477DD;
      border:1px solid #4477DD;
      line-height: 44px;
      text-align: center;
      font-size: 16px;
      border-radius: 5px;
      margin-top: 20px;
      margin-bottom: 20px;
    }
  }

  & .wallet-divider {
    background: #fbfbfb;
    height: 12px;
    width: 100%;
  }
}
</style>


